export function http_get(to, query, callback) {
    var url = domain + to;
    $.ajax({
        url: url,
        data: query,
        success: callback,
        error: (error) => {
            console.log(error);
        },
        headers: {
            "Appian-API-Key": apiKey,
            "Access-Control-Allow-Origin": "*"
        },
        method: "GET"
    });
}

