var utilities = require("./utilities");
// images references in the manifest
import "../../assets/icon-16.png";
import "../../assets/icon-32.png";
import "../../assets/icon-80.png";

/* global document, Office, Word */
var result,
  filteredResult,
  type,
  timer,
  selectedId,
  delay = 500;

Office.onReady(info => {
  // if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    new fabric["Dropdown"](document.getElementById("dropdown"));
    new fabric["TextField"](document.getElementById("textField"));
    new fabric["Spinner"](document.getElementById("loader"));
    new fabric["Button"](document.getElementById("backButton"), function() {
      $("#content").hide();
      $("#textField").show();
      $("#recordTable").show();
    });
    $("#type").change(fetchSearchDetails);
    $("#filterText").bind("keydown", function(e) {
      clearTimeout(timer);
      timer = setTimeout(function() {
        var filterText = $("#filterText").val();
        if (filterText != null || filterText != "") {
          filteredResult = result.filter(item => item[type.toLowerCase() + "Name"].toLowerCase().includes(filterText));
          if (filteredResult != null && filteredResult.length > 0) {
            var tableContent = getTableData(filteredResult);
            $("#recordTableBody").html(tableContent);
            $(".insertIcon").click(insertText);
            $(".showContent").click(showContent);
          } else $("#recordTableBody").html("");
        }
      }, delay);
    });
  // }
});

export async function insertText(event) {
  var value;
  var currentTagret = event.currentTarget;
  console.log(currentTagret)
  if(type=="Parameter"){
    console.log(currentTagret.getAttribute("data-value"))
    return Word.run(async context => {
      const paragraph = context.document
        .getSelection()
        .insertText(currentTagret.getAttribute("data-value"), Word.InsertLocation.after);
  
      // paragraph.font.color = "blue";
      await context.sync();
    });
  }else{
    console.log(currentTagret.getAttribute("data-index"))
    selectedId= currentTagret.getAttribute("data-index")
    utilities.http_get("clausedetails",
    {
      type:type,
      id:selectedId
    },
    response=>{
      console.log(response)
      var responseContent = document.createElement('div');
      responseContent.innerHTML = response.content
      console.log(responseContent.innerText)
    }
    )
  }

}
export function insertResponseContent(){
  console.log(document.getElementById('responseContentHtml').innerText)
}
export async function showContent(event) {
  
  var currentTagret = event.currentTarget;
  var id = currentTagret.getAttribute("data-index");
  selectedId =id;
  $("#recordTable").hide();
  $("#textField").hide();
  $("#loader").show();

  utilities.http_get(
    "clausedetails",
    {
      id: id,
      type: type
    },
    response => {
      $("#loader").hide();
      if (response == null || response.length == 0) {
        $("#banner").show();
        return;
      }
      var htmlContent =
        '<label class="ms-Label">' + response.name + '</label><div id="responseContentHtml" >' + response.content + "</div>";
      $("#contentHtml").html(htmlContent);
      $("#content").show();
      $("#addButton").click(insertResponseContent)
    }
  );
}

export async function fetchSearchDetails(event) {
  event.preventDefault();
  $("#searchWarning").html("");
  $("#banner").hide();
  $("#content").hide();
  $("#recordTable").hide();
  type = $("#type option:selected").text();
  if (type == null || utilities.dropdownTypeOptions.indexOf(type) < 0) {
    $("#searchWarning").html("Please select any one of the Types.");
    return;
  } else {
    $("#loader").show();
    utilities.http_get(
      "clausedetails",
      {
        type: type,
        pageNo:1
      },
      response => {
        console.log(response);
        result = response;
        filteredResult = response;
        $("#loader").hide();
        if (response == null || response.length == 0) {
          $("#banner").show();
          $("#textField").hide();
          return;
        }
        var tableContent = getTableData(response);
        $("#textField").show();
        $("#recordTableBody").html(tableContent);
        $(".insertIcon").click(insertText);
        $(".showContent").click(showContent);
        $("#recordTable").show();
      }
    );
  }
}

export function getTableData(data) {
  var values = data.values
  var tableContent = "";
  if (type == "Parameter") {
    for (var i = 0; i < values.length; i++) {
      tableContent +=
        '<tr><td><label title="' +
        values[i].name +
        '">' +
        values[i].name +
        '</label></td><td class="insertIcon" data-value="' +
        values[i].name +
        '"><button title="Insert" class="ms-Button ms-Button--hero"><i class="ms-Icon ms-Icon--AddTo"></i></button></td></tr>';
    }
  } else {
    for (var i = 0; i < values.length; i++) {
      tableContent +=
        '<tr><td class="showContent" data-index="' +
        values[i].id +
        '"><label title="' +
        values[i].name +
        '" class="pointer">' +
        values[i].name+
        '</label></td><td class="insertIcon" data-index="' +
        values[i].id +
        '"><button title="Insert" class="ms-Button ms-Button--hero"><i class="ms-Icon ms-Icon--AddTo"></i></button></td></tr>';
    }
  }
  return tableContent;
}

String.prototype.entitize = function(){
  var str = this.replace('>>','&gt&gt');
  str = str.replace('<<', '&lt&lt');
  return str;
}
